@font-face {
    font-family: 'Open Sans';
    src: url('../src/static/font/OpenSans-Regular.ttf') format('truetype');
    unicode-range: U+0041-005A, U+0061-007A, U+0030-0039;
}

@font-face {
    font-family: 'Noto Sans KR';
    src: url('../src/static/font/NotoSansKR-Regular.otf') format('opentype');
    unicode-range: U+AC00-U+D7A3;
}

.App{
    font-family: 'Open Sans', 'Noto Sans KR', Arial, 'Malgun Gothic', serif;
}